<template>
  <div class="login-wrap">
    <div class="title">
      <img class="title-logo" src="https://img.yzcdn.cn/vant/logo.png">
      <h2 class="title-text">注册</h2>      
      <p class="title-desc">视频卡、星巴克、肯德基等生活服务类</p>
      <p class="title-desc">礼品卡、代金券、权益交易平台</p>
    </div>
    <van-cell-group>
      <van-field 
        v-model="tel" 
        placeholder="请输入手机号" 
        label="手机号" 
        type="tel" 
        left-icon="phone-o"
        clearable 
        maxlength='11'
        autocomplete="off"
      />
      <van-field 
        v-model="code" 
        placeholder="请输入手机验证码" 
        label="验证码" 
        type="tel" 
        left-icon="chat-o"
        clearable 
        maxlength='6'
        autocomplete="off"
      />
      <van-field 
        v-model="password" 
        placeholder="请输入密码" 
        label="密码" 
        type="password" 
        left-icon="closed-eye"
        autocomplete="off"
      />
      <van-field 
        v-model="againPassword" 
        placeholder="请再次输入密码" 
        label="确认密码" 
        type="password" 
        left-icon="closed-eye"
        autocomplete="off"
      />
    </van-cell-group>
    <div class="tip">
      <span class="fl">
        <van-checkbox v-model="checked" checked-color="#07c160" direction="horizontal">
          我已阅读，理解并接受
        </van-checkbox>
      </span>
      <a class="fl green" @click="goPage('/agreement')">【会员协议】</a>
      <a class="fr green" @click="goPage('/login')">直接登录</a>
    </div>
    <van-button type="primary" block @click="register">注册</van-button>
  </div>
</template>

<script>
export default {
  name: 'register',
  data () {
    return {
      checked: true,
      tel: '',
      code: '',
      password: '',
      againPassword: ''
    }
  },
  methods: {
    goPage () {
      this.$router.push({ path: '/agreement' })
    },
    isValid () {
      if (!this.tel) {
        this.$toast.fail('请输入手机号')
        return false
      }

      if (!this.code) {
        this.$toast.fail('请输入验证码')
        return false
      }

      if (!this.tel.match(/^1\d{10}$/)) {
        this.$toast.fail('请输入正确的手机号')
        return false
      }

      if (this.password !== this.againPassword) {
        this.$toast.fail('两次密码不相同')
        return false
      }

      return true
    },
    register () {
      // if (!this.isValid) {
      //   return false
      // }
      // this.$http.register({
      //   tel: this.tel,
      //   code: this.code,
      //   password: this.password,
      //   passagainPasswordword: this.againPassword
      // }).then(res => {
      //   if (res.code === 0) {
          this.$toast.success('注册成功，请登录')

          setTimeout(() => {
            this.$router.push({ path: '/login' })
          }, 1000)
      //   } else {
      //     this.$toast.fail(res.message)
      //   }
      // }).catch(err => {
      //   console.log(err)
      // })
    }
  }
}
</script>



